<template>
  <div>
    <div class="flex justify-end">
      <button
        class="px-4 py-2 bg-purple-500 text-white font-semibold rounded"
        @click="showAddModal = true"
      >
        + Add
      </button>
    </div>
    <div v-if="codes" class="mt-4">
      <div v-if="codes.length > 0">
        <code-card v-for="code in codes" :key="code.id" class="mt-4" :code="code" />
      </div>
      <div v-else class="px-4 py-4 text-center bg-gray-200 rounded-lg">
        <p class="text-gray-600">
          No codes were found
        </p>
      </div>
    </div>
    <div v-else />

    <form-modal :open.sync="showAddModal" :form="form" title="Add Code" @input="onAdd" />
  </div>
</template>

<script>
import api from '../../mixins/api';
import CodeForm from '../../components/forms/codes/CodeForm';
import FormModal from '../../components/modals/FormModal';
import CodeCard from '../../components/cards/codes/CodeCard';

export default {
  components: {
    FormModal,
    CodeCard,
  },
  data: () => ({
    codes: null,
    form: CodeForm,
    showAddModal: false,
  }),
  async mounted() {
    try {
      const { data } = await api.getCodes();
      this.codes = data;
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    async onAdd(form) {
      try {
        const { data } = await api.createCode(form);
        if (this.codes) {
          this.codes.push(data);
        } else {
          this.codes = [data];
        }
      } catch (error) {
        console.log(error);
      }
    },
    async onUpdate(id, form) {
      try {
        const code = await api.updateCode(id, form);
      } catch (error) {
        console.log(error);
      }
    },
    async onDelete(id) {
      try {
        this.codes = await api.deleteCode(id);
      } catch (error) {
        console.log(error);
      }
    },
  },
  head() {
    return {
      title: 'Codes',
    };
  },
};
</script>
